.front_content{
      width: 650px;
      height: 650px;
      position: relative;
      margin: 3rem;
}

.banner_front{
      width: 326px;
      height: 100%;
      padding: 1rem;
      -webkit-backdrop-filter: blur(5px);
      -moz-backgrop-filter: blur(5px);
      backdrop-filter: blur(5px);
      box-shadow: 0 0 20px rgba(0,0,0,.2), 2rem 2rem 25px rgba(0,0,0,.2);
}

.banner_front__img{
      width: 306px;
      height: 630px;
      background: var(--dark-400);
      overflow: hidden;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
}

.banner_front__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.banner_front__img:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(27,27,55, .1) 30%, var(--dark-400) 85%);
      top: 0;
      left: 0;
}

.banner_front__img:after{
      width: 100%;
      height: 5px;
      background: linear-gradient(90deg, var(--light) 33%, var(--second) 33%, var(--second) 66%, var(--theme) 66%);
      left: 0;
      bottom: 0;
}

.banner_front__menu{
      width: 100%;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      left: 0;
      bottom: 3rem;
      gap: 1rem;
}

.banner_front__menu a{
      width: 38px;
      height: 38px;
      background: var(--dark-500);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: relative;
      transition: .2s;
}

.banner_front__menu a *{
      font-size: 1.8rem;
      color: var(--second);
}

.banner_front__menu a:hover *{
      color: var(--light);
}

.banner_front__menu a:before{
      content: attr(data-tooltip);
      text-transform: uppercase;
      color: var(--second);
      padding: .5rem 1rem;
      background: var(--dark-500);
      top: -3rem;
      left: 50%;
      transform: translateX(-50%);
      visibility: hidden;
      opacity: 0;
      transition: .2s;
      white-space: nowrap;
}

.banner_front__menu a:hover:before{
      visibility: visible;
      opacity: 1;
}

.wrapper_user{
      width: 385px;
      height: 450px;
      position: absolute;
      top: 118px;
      left: 260px;
}

.user_avatar{
      width: 155px;
      height: 150px;
      position: relative;
      z-index: 3;
      border-left: solid 5px var(--light);
      background: var(--dark-400);
      box-shadow: 2rem 2rem 20px rgba(0,0,0,.3);
      transform: translateX(-80px);
}

.user_avatar:before{
      width: 5px;
      height: 100%;
      left: -5px;
      top: 0;
      background: linear-gradient(var(--light) 33%, var(--second) 33%, var(--second) 66%, var(--theme) 66%);
}

.user_avatar__img,
.user_avatar__img img{
      width: 150px;
      height: 150px;
}

.user_relation__btn{
      width: 42px;
      height: 42px;
      background: var(--dark-500);
      border-radius: 50%;
      position: absolute;
      right: -21px;
      top: calc(50% - 21px);
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_relation__btn *{
      font-size: 2rem;
      color: var(--theme);
}

.user_relation__btn:before{
      width: 56px;
      height: 56px;
      border-radius: 50%;
      box-shadow: 0 0 0 .4rem var(--second) inset;
}

.user_content{
      width: 385px;
      height: 375px;
      position: absolute;
      left: 0;
      top: 75px;
      background: var(--dark-400);
      box-shadow: 0 0 20px rgba(0,0,0,.1), 20px 20px 20px rgba(0,0,0,.3);
      padding: 2rem;
}

.user_content:before{
      width: 100%;
      height: 5px;
      background: linear-gradient(90deg, var(--light) 33%, var(--second) 33%, var(--second) 66%, var(--theme) 66%);
      left: 0;
      bottom: 0;
}

.user_content__data{
      width: 100%;
      height: 280px;
      display: flex;
      align-items: center;
      flex-direction: column;
}

.user_content__data h1{
      font-size: 3rem;
      font-family: var(--ft-1);
      padding: 0 1rem;
      font-weight: 400;
}

.user_content__data span{
      font-size: 1.4rem;
}

.user_data__about{
      margin-top: 2rem;
      height: 145px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--dark-500) var(--theme);
}

.user_data__about p{
      font-family: var(--ft-3);
      font-size: 1.2rem;
      text-align: center;
      color: var(--second);
}

.user_content__social{
      width: 100%;
      height: 50px;
      margin-top: 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.user_content__menu{
      width: 130px;
      height: 110px;
      position: absolute;
      left: 0;
      top: 95px;
      transform: translateX(-100%);
      overflow: hidden;
}

.user_content__menu ul{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      gap: 1px;
}

.user_content__menu ul li{
      width: 100%;
      height: 36px;
      border-left: solid 5px var(--light);
      background: var(--dark-300);
      display: flex;
      align-items: center;
      gap: .5rem;
      text-transform: uppercase;
      position: relative;
      transform: translateX(calc(100% - 41px));
      transition: all .4s cubic-bezier(0,.31,.27,.97);
      cursor: pointer;
}

.user_content__menu ul li:hover{
      transform: translatex(0);
}

.user_content__menu ul li:nth-child(2){
      border-left: solid 5px var(--second);
      background: var(--dark-200);
}

.user_content__menu ul li:nth-child(3){
      border-left: solid 5px var(--theme);
      background: var(--dark-100);
}

.user_content__menu ul li i{
      width: 36px;
      height: 36px;
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_content__menu ul li i *{
      font-size: 1.8rem;
}

.user_content__menu ul li:nth-child(2) i *{
      color: var(--second);
}

.user_content__menu ul li:nth-child(3) i *{
      color: var(--theme);
}

.pages,
.wrapper_relation{
      width: 380px;
      height: 340px;
      background: var(--dark-400);
      position: absolute;
      border-radius: .3rem;
      box-shadow: 0 0 20px rgba(0,0,0,.3), 2rem 2rem 25px rgba(0,0,0,.2);
      top: 208px;
      left: 280px;
      z-index: 200;
      visibility: hidden;
      opacity: 0;
}

.pages.show,
.wrapper_relation.show{
      animation: show_page .3s ease-in-out forwards;
}

@keyframes show_page{
      0%{
            visibility: hidden;
            opacity: 0;
      }
      100%{
            visibility: visible;
            opacity: 1;
      }
}

.pager_content{
      width: 100%;
      height: 100%;
      position: relative;
      padding: 1rem;
}

.pager_inner{
      width: 358px;
      height: 266px;
      background: var(--dark-500);
      padding: .5rem;
      border-radius: .6rem;
      margin-top: 4.6rem;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme);
}

.pager_inner::-webkit-scrollbar{
      border-radius: 8px;
}

.pager_inner::-webkit-scrollbar-thumb{
      border-radius: 8px;
}

.pager_content:before{
      width: 14px;
      height: 14px;
      background: var(--dark-400);
      left: -8px;
      transform: rotate(-45deg);
      border-radius: .2rem;
}

.wrapper_relation{
      width: 270px;
      height: 380px;
      display: flex;
      flex-direction: column;
      padding: 2rem;
      align-items: center;
      left: 380px;
      top: 10px;
      border: none;
}

.wrapper_relation:before{
      width: 14px;
      height: 14px;
      background: var(--dark-400);
      left: -7px;
      transform: rotate(-45deg);
      border-radius: .2rem;
      top: 175px;
}

.relation_avatar,
.relation_avatar img{
      width: 128px;
      height: 128px;
      border-radius: 50%;
}

.relation_avatar{
      display: flex;
      margin-top: 3rem;
      box-shadow: 0 0 0 1rem var(--dark-500);
      overflow: hidden;
}

.wrapper_relation h3{
      font-size: 2.3rem;
      font-family: var(--ft-1);
      color: var(--theme);
      margin-top: 1rem;
      font-weight: 400;
}

.wrapper_relation span,
.wrapper_relation a{
      text-transform: uppercase;
      color: var(--second);
      border-radius: 999px;
      background: var(--dark-500);
      margin-top: 1rem;
      padding: .4rem 1.5rem;
}

.relation_about{
      margin-top: 1rem;
      height: auto;
      min-height: 40px;
      max-height: 80px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--dark-500) var(--theme)
}

.relation_about p{
      text-align: center;
      font-size: 1.2rem;
}